* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Montserrat', sans-serif;
}

.app {

  background-size: cover;
  background-position: bottom;
  transition: 0.4 ease;
}

main {
  min-height: 100vh;
  background-image:  url('./image/5445590.jpg');
  padding: 25px;
}

.search-box {
  width: 100%;
}

.search-box .search-bar {
  display: block;
  width: 50%;
  padding: 15px;
  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color: rgb(255, 255, 255);
  border-radius: 20px;
  margin-top: 55px;
  margin-left: auto;
  margin-right: auto;
  
  box-shadow: 0px 5px rgba(0, 0, 0, 0.2);
  
  color: #313131;
  font-size: 20px;

  transition: 0.4s ease;
}

.search-box .search-bar:focus {
  background-color: rgba(255, 255, 255, 0.75);
}

.location-box .location {
  color: #FFF;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.location-box .date {
  color: #FFF;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}

.weather-box {
  text-align: center;
  background-image: url('https://source.unsplash.com/1600x900/?" + (galaxy) + "');
  height: fit-content;

}

.weather-box .temp {
  display: flex;
  flex-direction:column;
  width:max-content;
  gap: 20px;
  margin: auto;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 16px;
  border: solid 4px white;

  padding: 15px 15px;

  color: #FFF;
  font-size: 30px;
  font-weight: 300;

  text-shadow: 3px 3px rgba(50, 50, 70, 0.596);
  text-align: center;
  box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
}

.weather-box .weather {
  color: #FFF;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}


